import React,{Component} from 'react'
import {Button,Table,Modal,message} from 'antd'
import {UserOutlined,ExclamationCircleOutlined } from '@ant-design/icons';

import {requestUserList,requestRemoveUser,requestAddUser,requestUpdateUser} from '../../../../ajax/index'
import CollectionForm from './CollectionForm'
import './User.less'

const { Column} = Table
const { confirm } = Modal;
export default class User extends Component{
    state = {
        roleObject:{},
        roleArr:[], // 
        addUserVisible: false,
        updateUsrVisible: false,
        nowClickUserInfo: {},
        data : []
    }
    componentDidMount() {
        this.getUserList()
    }
    showConfirm = (record) => {  // 
        console.log(record)
        confirm({
            title: `${record.name}`,
            icon: <ExclamationCircleOutlined />,
            content: '',
            onOk: async () => {
                console.log(record._id)
                const result = await requestRemoveUser(record._id)
                if (result.code ===0) {
                    console.log(result)
                    message.success('')
                    this.getUserList()
                } else {
                    message.error('')
                }
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }
    timeFunc = (timeNum) => {
        let dateStr = ''
        let timeStr = ''
        if (timeNum) {
            let time = new Date(timeNum)
            dateStr += time.getFullYear()+""
            dateStr += time.getMonth()+1+""
            dateStr += time.getDate()+""
            timeStr += time.getHours()+":"
            timeStr += time.getMinutes()+":"
            timeStr += time.getSeconds()
            return {timeStr,dateStr}
        }
        return {dateStr:'',timeStr:'',}
    }
    getUserList = async () =>{
        const result = await requestUserList()
        if (result.code===0) {
            const data = result.data.users
            const roleArr = result.data.roles
            let roleObject = {}
            roleArr.forEach((role) =>{
                roleObject[role._id] = role.name
            })
            console.log(roleObject)
            data.map((item,index) =>{
                item.key = index
                const timeString = this.timeFunc(item.create_time)
                item.createTime = timeString.dateStr+'  '+timeString.timeStr
                item.name = item.username
                item.roleOfUsr = roleObject[item.role_id]
            })
            await this.setState({
                data,
                roleArr,
                roleObject
            })
        } else {
            message.error("")
        }
    }
    handleCancel = async () => { // 
        this.FormRef.current.resetFields()
        await this.setState({
            addUserVisible: false,
            updateUsrVisible: false,
            removeUsrVisible: false,
            nowClickUserInfo: {},
        });
    }
    updateHandleOk = async () =>{ // 
        this.FormRef.current.validateFields()
            .then(async (success)=>{
                const {username,phone,email} = success
                const role_id = success.role
                const {_id} = this.state.nowClickUserInfo
                const result = await requestUpdateUser({_id,username,phone,email,role_id})
                if (result.code === 0) {
                    message.success("")
                    this.setState({
                        updateUsrVisible: false,
                    });
                    this.getUserList()
                    this.FormRef.current.resetFields()
                } else {
                    message.error('')
                }
            })
            .catch((error)=>{
                console.log(error)
            })
    }
    addHandleOk = async () => { // 
        this.FormRef.current.validateFields()
            .then(async (success)=>{
                const {username,password,phone,email} = success
                const role_id = success.role
                console.log(username,password,phone,email,role_id)
                const result = await requestAddUser({username,password,phone,email,role_id})
                if (result.code ===0) {
                    message.success('')
                    this.setState({
                        addUserVisible: false,
                    });
                    this.FormRef.current.resetFields()
                    this.getUserList()
                } else if(result.code ===1) {
                    message.error(`${result.message}`)
                }
            })
            .catch((error)=>{
                console.log(error)
            })
    };
    updateUserModal = async (text, record) => { // 
        await this.setState({
            updateUsrVisible: true,
            nowClickUserInfo:record
        })
        console.log(record,text,'')
    };
    addNewUserModel = async () =>{ // 
        await this.setState({
            addUserVisible: true,
        })
    }
    render(){
        return(
            <div className='User-wrap'>
                <div className='User'>
                    <div className='User-header-wrap'>
                        <div className='User-header'>
                            <Button type='primary' onClick={this.addNewUserModel}></Button>
                        </div>
                    </div>
                    <div className='User-line-wrap'>
                        <div className='User-line'></div>
                    </div>
                    <div className='User-main-wrap'>
                        <div className='User-main'>
                            <Table dataSource={this.state.data}
                                   pagination={{pageSize:8}}>
                                <Column title="" dataIndex="name" key="name" render={(text, record) => (
                                    <span><UserOutlined />{"   "+record.name}</span>
                                )}/>
                                <Column title="" dataIndex="email" key="email" />
                                <Column title="" dataIndex="phone" key="phone" />
                                <Column title="" dataIndex="createTime" key="createTime" />
                                <Column title="" dataIndex="roleOfUsr" key="roleOfUsr" />
                                <Column title="" key="action"
                                    render={(text, record) => (
                                        <span>
                                          <Button onClick={()=>{
                                             this.updateUserModal(text, record)
                                          }} type='primary' style={{ marginRight: 16 }}></Button>
                                          <Button onClick={()=>{
                                              this.showConfirm(text, record)
                                          }}></Button>
                                        </span>
                                    )}/>
                            </Table>
                        </div>
                    </div>
                </div>
                <Modal
                    title=''
                    destroyOnClose={true}
                    visible={this.state.addUserVisible}
                    onOk={this.addHandleOk}
                    onCancel={this.handleCancel}>
                    <CollectionForm nowClickUserInfo={{}} roleArr={[...this.state.roleArr]} FormDate={(FormRef) =>{this.FormRef = FormRef} }/>
                </Modal>
                <Modal
                    title=""
                    destroyOnClose={true}
                    visible={this.state.updateUsrVisible}
                    onOk={this.updateHandleOk}
                    onCancel={this.handleCancel}>
                    <CollectionForm nowClickUserInfo={{...this.state.nowClickUserInfo}} roleArr={[...this.state.roleArr]} FormDate={(FormRef) =>{this.FormRef = FormRef} }/>
                </Modal>
            </div>
        )
    }
}
